import React from 'react';
import { Grid, Typography, Tooltip } from '@material-ui/core';
import useHolderData from '../../hooks/useHolderData';
import StatCard from './EcosystemStatCard.js';
import TigerShark from '../../assets/img/tigershark.png';
import Shrip from '../../assets/img/shrimp.png';
import moment from 'moment';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';

const EcosystemBreakdown = () => {
    const {data, loading} = useHolderData();

    const ecosystem = [
        { type: "Shrimp", count: data?.shrimp?.count, img: true, src: Shrip, width: 30, totalAXN: data?.shrimp?.totalAxn },
        { type: "Crabs", count: data?.crab?.count, emoji: "ü¶Ä", totalAXN: data?.crab?.totalAxn },
        { type: "Fish", count: data?.fish?.count, emoji: "üê†", totalAXN: data?.fish?.totalAxn },
        { type: "Octopuses", count: data?.octopus?.count, emoji: "üêô", totalAXN: data?.octopus?.totalAxn },
        { type: "Dolphins", count: data?.dolphin?.count, emoji: "üê¨", totalAXN: data?.dolphin?.totalAxn },
        { type: "Tiger Sharks", count: data?.tigerShark?.count, img: true, src: TigerShark, width: 50, totalAXN: data?.tigerShark?.totalAxn  },
        { type: "Great White Sharks", count: data?.greatWhite?.count, emoji: "ü¶à", totalAXN: data?.greatWhite?.totalAxn },
        { type: "Whales", count: data?.whale?.count, emoji: "üê≥", totalAXN: data?.whale?.totalAxn },
    ]

    return (
        <div style={{padding: '2%'}}>
            <Typography variant="h4" align="center" color="primary" style={{ fontWeight: '100' }}>
                Axion Ecosystem  
                {!loading && data.totals && (
                   <sup>
                        <Tooltip title={`Last Updated: ${moment(data.totals.last_updated).format("MMM Do, YYYY, h:mm a")}. Ecosystem is updated hourly.`} classes={{ tooltip: 'tooltip' }} placement="right">
                            <InfoOutlinedIcon style={{ fontSize: '0.7rem', cursor: 'pointer' }} color="secondary" />
                        </Tooltip>
                   </sup>
                )}
            </Typography>

            {!loading && data.totals && (
                <>
                    <Typography variant="subtitle1" align="center" color="primary" style={{ fontWeight: '100' }}>{data.totals.holders.toLocaleString()} addresses hold at least 1 AXN.</Typography>
                    
                    <Tooltip title="Does not include contracts or dev fund" classes={{ tooltip: 'tooltip' }} placement="bottom">
                        <Typography variant="subtitle1" align="center" color="primary" style={{ fontWeight: '100', maxWidth: '325px', margin: '0 auto', cursor: 'pointer' }}>
                            Ecosystem Total: {Math.round(data.totals.held_axn).toLocaleString()} AXN
                        </Typography>
                    </Tooltip>
                </>
            )}
          
            <div style={{margin: '2%'}}>
                <Grid container justify="space-evenly" spacing={3}>
                    {ecosystem.map(e => (
                        <Grid item lg={3} md={6} xs={10} key={e.type}>
                            <StatCard type={e.type} count={e.count} loading={loading} emoji={e.emoji} isImage={e.img ?? false} src={e.img ? e.src : null} imgWidth={e.width} total={e.totalAXN}/>
                        </Grid>
                    ))}
                </Grid>
            </div>
          
            <br/>
        </div>
    )
}

export default EcosystemBreakdown;