import React from 'react';
import { Typography } from '@material-ui/core';

const VERSION = "v0.7.0";

const Home = () => {
    const openWebsite = () => window.open("https://axion.network", "_blank");
    const openWhitepaper = () => window.open("https://axion.network/pdf/axion-whitepaper.pdf", "_blank");
    const openDiscord = () => window.open("https://discord.gg/axion", "_blank");
    const openUniswap = () => window.open("https://app.uniswap.org/#/swap?outputCurrency=0xed1199093b1abd07a368dd1c0cdc77d8517ba2a0", "_blank");

    return (
        <div className="footer">
            <Typography align="center" variant="subtitle2" style={{ color: "#FFF" }}>Beta {VERSION} - Created by<span style={{ color: '#00D637' }}> Some Green Guy</span></Typography>
            
            <div className="footerLinks">
                <Typography onClick={openWebsite} variant="subtitle2" style={{ color: "#FFF" }} display="inline" className="clickableLink">Axion Website</Typography>
                
                <span style={{ color: "#BABABA" }}>&nbsp;|&nbsp;</span>
                
                <Typography onClick={openWhitepaper} variant="subtitle2" style={{ color: "#FFF" }} display="inline" className="clickableLink">Whitepaper</Typography>
                
                <span style={{ color: "#BABABA" }}>&nbsp;|&nbsp;</span>
                
                <Typography onClick={openDiscord} variant="subtitle2" style={{ color: "#FFF" }} display="inline" className="clickableLink">Discord</Typography>

                <span style={{ color: "#BABABA" }}>&nbsp;|&nbsp;</span>

                <Typography onClick={openUniswap} variant="subtitle2" style={{ color: "#FFF" }} display="inline" className="clickableLink">Uniswap</Typography>
            </div>
        </div>
    )
}

export default Home;